import { Link, Typography } from '@material-ui/core';
import SafeGrid from '../../../../layout/SafeGrid/SafeGrid.component';

const PortfolioItem = ({
	desc,
	href,
	imgSrc,
}: {
	desc: string;
	href: string;
	imgSrc: string;
}) => {
	return (
		<Link href={href} target={'_blank'}>
			<SafeGrid fillContainer={true}>
				<img alt={desc} src={imgSrc} style={{ borderRadius: 16, width: '100%' }} />
				<Typography variant={'caption'} children={desc} />
			</SafeGrid>
		</Link>
	);
};

export default PortfolioItem;
