import { createStyles, makeStyles, Theme } from '@material-ui/core';
import { ISafeGridStyleProps } from './SafeGrid.types';
import { type } from 'os';

const useStyles = ({
	safeDirection,
	safePadding,
	safeSpacing,
	safeFillContainer,
	safeJustifyContent,
	safeWrap,
	safeItemsNumber,
}: ISafeGridStyleProps) =>
	makeStyles((theme: Theme) =>
		createStyles({
			root: {
				display: 'flex',
				flexDirection: safeDirection,
				flexWrap: safeWrap,
				padding: safePadding,
				marginRight: safeDirection === 'column' ? -safeSpacing : 0,
				marginBottom: safeDirection === 'row' ? -safeSpacing : 0,
				width: '100%',
			},
			withBackground: {
				background: theme.palette.background.paper,
			},
			mainColumn: {
				margin: 'auto !important',
				maxWidth: theme.breakpoints.values.lg,
			},
			childColumn: {
				flex: safeFillContainer ? '1' : 'unset',
				flexDirection: safeDirection,
				marginBottom: safeSpacing,
				'& :last-of-type': {
					marginBottom: 0,
				},
			},
			childRow: {
				display: safeJustifyContent ? 'flex' : 'block',
				flex: safeFillContainer ? '1' : 'unset',
				flexDirection: safeDirection,
				alignContent: safeJustifyContent,
				marginBottom: safeSpacing,
				marginRight: safeSpacing,
				'& :last-of-type': {
					marginRight: 0,
				},
			},
			itemWidth: {
				flex: 'unset',
				width:
					safeItemsNumber !== 1
						? `calc((100% - ${
								(typeof safeSpacing === 'string'
									? parseInt(safeSpacing, 10)
									: safeSpacing) * safeItemsNumber
						  }px) / ${safeItemsNumber})`
						: 'unset',
			},
		})
	);

export default useStyles;
