import React from 'react';

import { HashRouter, Route, Switch } from 'react-router-dom';

import { Routes } from './routes';
import AboutContact from '../components/systems/AboutContact';
import Portfolio from '../components/systems/Portfolio';

const Router = () => {
	return (
		<HashRouter>
			<Switch>
				<Route exact path={Routes.PORTFOLIO} component={Portfolio}></Route>
				<Route exact path={Routes.HOME} component={AboutContact}></Route>
			</Switch>
		</HashRouter>
	);
};

export default Router;
